import React, { useState, useEffect } from "react"
import firebase from "../Firebase/firebaseConfig"
import TextField from "@material-ui/core/TextField"
import { makeStyles } from "@material-ui/core/styles"
import Button from "@material-ui/core/Button"

const AuthUser = () => {
    const [auth, setAuth] = useState(false)
    const [valueInputs, SetvalutInputs] = useState({ email: "", password: "" })
    const onHandleInputs = (e) => {
        SetvalutInputs({
            ...valueInputs,
            [e.target.type]: e.target.value,
        })
    }

    useEffect(() => {}, [])
    const onSignUpAuthHandle = (e) => {
        e.preventDefault()
        const { email, password } = valueInputs
        console.log(valueInputs)
        firebase
            .auth()
            .createUserWithEmailAndPassword(email, password)
            .then((user) => {
                setAuth(true)
                console.log("create user", user)
                SetvalutInputs({
                    email: "",
                    password: "",
                })
            })
            .catch((error) => {})
    }
    const onLogInAuthHandle = (e) => {
        e.preventDefault()
        const { email, password } = valueInputs
        firebase
            .auth()
            .signInWithEmailAndPassword(email, password)
            .then((user) => {
                setAuth(true)
                console.log("login user", user)
            })
            .catch((error) => {
                var errorCode = error.code
                var errorMessage = error.message
            })
    }
   
    return (
        <div>
            <form>
                <TextField
                    id="outlined-Email-input"
                    label="email"
                    value={valueInputs.email}
                    type="Email"
                    autoComplete="current-password"
                    variant="outlined"
                    onChange={onHandleInputs}
                />
                <TextField
                    id="outlined-password-input"
                    label="password"
                    value={valueInputs.password}
                    type="password"
                    autoComplete="current-password"
                    variant="outlined"
                    onChange={onHandleInputs}
                />
                {valueInputs.email}
                <Button type="submit" value="login" color="inherit" onClick={onLogInAuthHandle}>
                    Login
                </Button>
                <Button type="submit" value="signIn" color="inherit" onClick={onSignUpAuthHandle}>
                    Sign In
                </Button>
            </form>
        </div>
    )
}
export default AuthUser
